import "./movie-list.css";
import {Card, Result, Switch} from "antd";
import React from "react";
import axios from "axios";
import Pagination from "../pagenation";
import {MOVIE_KEY} from "../key";

function MovieList(prop) {
    const [movies, setMovies] = React.useState([]);
    const limit = 20; //    
    const [page, setPage] = React.useState(1);
    const offset = (page - 1) * limit; //    (index)
    let searching = prop.searchString;
    let select = prop.selected;

    let isSelect;
    if (select === "movieNm") {
        isSelect = "movieNm";
    } else if (select === "directorNm") {
        isSelect = "directorNm";
    } else {
        isSelect = "movieNm"
    }

    //console.log("", searching);
    const url = `http://kobis.or.kr/kobisopenapi/webservice/rest/movie/searchMovieList.json?key=${MOVIE_KEY}&curPage=1&itemPerPage=50&${isSelect}=${searching}`;

    React.useEffect(function () {
        axios
            .get(url)
            .then(function (result) {
                //console.log("", result);
                const movies = result.data.movieListResult.movieList;
                const filterMv = movies.filter((param) => param.repGenreNm !== "()");
                setMovies(filterMv);

            })
            .catch(function (err) {
                console.log("", err);
            });
    }, [prop.searchString, isSelect]);

    return (
        <div>
            <section className="movie-list-card">
                {
                    movies
                        .slice(offset, offset + limit)
                        .map((movies, index) => {
                            //slice index 0 8  map 
                            return (
                                <Card
                                    title={`${movies.movieNm}`}
                                    type="inner"
                                    className="info-card"
                                    key={index}
                                    >
                                   
                                    <p className="info"> : {movies.genreAlt}</p>
                                    <p className="info"> : {movies.repNationNm}</p>
                                    <p className="info"> : {movies.prdtYear}</p>
                                    <p className="info"> : {movies.prdtStatNm}</p>
                                    <p className="info"> : {
                                            movies
                                                .directors
                                                .map((name) => {
                                                    return name.peopleNm
                                                })
                                        }</p>
                                </Card>

                            );
                        })
                }
            </section>
            <div className="pagination">
                <Pagination total={movies.length}
                    //pagenation prop 
                    limit={limit} page={page} setPage={setPage}/>
            </div>
        </div>

    )

}

export default MovieList;