import React from "react";
import axios from "axios";
import "./boxoffice.css";
import { Card, Switch } from "antd";
import dayjs from "dayjs";
import { MOVIE_KEY } from "../key";
import Pagination from "../pagenation";


function Boxoffice(prop) {
  const [boxoffice, setBoxoffice] = React.useState([]);
  const limit = 10; // 10  
  const [page, setPage] = React.useState(1);
  const offset = (page - 1) * limit;

  let date = new Date();

  function getFormatDate(date) {
    let year = date.getFullYear(); //yyyy
    let month = 1 + date.getMonth(); //M
    month = month >= 10 ? month : "0" + month; //month  
    let day = date.getDate() - 1; //d ->   
    day = day >= 10 ? day : "0" + day; //day  
    return year + month + day;
  }

  date = getFormatDate(date);
  //console.log("date", date);

  const url = `http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json?key=${MOVIE_KEY}&targetDt=${date}`;

  React.useEffect(() => {
    axios
      .get(url)
      .then((result) => {
       // console.log("", result);
        const boxoffice = result.data.boxOfficeResult.dailyBoxOfficeList;
        setBoxoffice(boxoffice);
      })
      .catch((err) => {
        console.log("!", err);
      });
  }, []);

  return (
    <div>
      <section className="rank-list-card">
        {boxoffice.slice(offset, offset + limit).map((boxoffice, index) => {
          return (
           
              <Card title={`<${boxoffice.rank}>`} type="inner" align="center" className="boxoffice-card" key={index}>
                <Card
                  type="inner"
                  title={`${boxoffice.movieNm}`}
                  align="center"
                 
                >
                   <Switch checkedChildren="" unCheckedChildren="" />
                  <p>  : {boxoffice.openDt}</p>
                  <p>  : {boxoffice.audiCnt}</p>
                  <p>  : {boxoffice.audiAcc}</p>
                </Card>
              </Card>
           
          );
        })}
      </section>
      <div className="pagination">
        <Pagination
          total={boxoffice.length} //pagenation prop 
          limit={limit}
          page={page}
          setPage={setPage}
        />
      </div>
    </div>
  );
}

export default Boxoffice;

